.fw-medium{
    font-weight: 500;
}

$font-base:16px;
.font-s{
    font-size:  $font-base *1.25; //20px
}

.font-m{
    font-size:  $font-base *1.5; //24px
}
.font-l{
    font-size:  $font-base * 1.75; //28px
}

.font-xl{
    font-size:  $font-base * 2; //32px
}

.font-xxl{
    font-size:  $font-base * 2.25; //36px
}
.font-2xl{
    font-size:  $font-base * 2.5; //40px
}
.swiper-img{
    width: 350px;
    height: 350px;
}
.h{
    &-400{
        height: 400px;
    }
    &-84{
        height: 84px;
    }
    &-350{
        height: 350px;
    }
}
.w{
    &-160{
        width: 160px;
    }
    &-165{
        width: 165px;
    }
    &-350{
        width: 350px;
    }
    &-540{
        width: 540px;
    }

}
.top-80{
    top: 80px;
}
.hoverOpacity:hover img{
    opacity: 0.5;
}