.timeline-section{
    position: relative;
    margin: 301px 0;
    &::after{
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        height: 3px;
        background-color: red;
    }
}

.timeline-pointer{
    position: relative;
    display: flex;
    justify-content: center;
    &::before{
        content: '';
        position: absolute;
        left: 50%;
        width: 8px;
        height: 8px;
        border-radius: 50%;
        transform: translate(-50%,-25%);
        background-color: #fff;
        border:3px solid red;
    }
}